import React, { Component } from "react";
import pic from "../../assets/Muszimuszla/projects.png";
import { Link } from "react-router-dom";

class Projects extends Component {
  state = {};
  render() {
    return (
      <div className="oneBox reverse">
        <div>
          <h1> Example projects </h1>
          <h6>What you can see</h6>
          <p>
            {" "}
            Website implementation using React.js framework. All
            graphics are created by me.{" "}
          </p>
          <h6>Pokedex</h6>
          <p>
            Website visualizing 1st generation Pokedex. Developed with
            the PokeAPI and React.js framework. Graphics have been prepared specially for this project.
          </p>
          <Link to="/pokedex" className="btn btn-link btn-md">
            Check it out
          </Link>
          <h6>Currency exchange app</h6>
          <p>
            Website allowing to create list of transactions, the amounts of which are automatically 
            converted based on the given exchange rate. Implementation was made using React.js framework with Redux and Sass. 
          </p>
          <a href="https://muszimuszla.github.io/currencyAppDemo" className="btn btn-link btn-md">
            Check it out
          </a>
          <h6>Tracking system</h6>
          <p>
            Implementation of a software for tracking device with dedicated web
            service. Front-end implementation was done with 
            Vue.js framework. Back-end development was made using Node.js
            platform and Express.js framework, as a database I've decided to use
            Mongo DB. 
          </p>
        </div>
        <img src={pic} alt="Pic" />
      </div>
    );
  }
}

export default Projects;
