import React, { Component } from "react";
import pic from "../../assets/Muszimuszla/hello.png";
import SocialMedia from "./SocialMedia";

class Hello extends Component {
  state = {};
  render() {
    return (
      <div className="oneBox columnBox">
        <img src={pic} alt="Pic" />
        <SocialMedia />
        <div>
          <h1> Hello </h1>
          <p style={{ textAlign: "center" }}>
            {" "}
            My name is Madzia. If you are here I hope you would like to know me
            better. Let me shortly tell you my story. I live in Poland, near the
            beautiful Cracow city. I'm a programmer and an illustrator with love
            to coffee and chocolate.{" "}
          </p>
        </div>
      </div>
    );
  }
}

export default Hello;
