import React, { Component } from "react";
import pic from "../../assets/Muszimuszla/technologies.png";

class Technologies extends Component {
  state = {};
  render() {
    return (
      <div className="oneBox">
        <img src={pic} alt="Pic" />
        <div>
          <h1> Technologies </h1>
          <p>
            {" "}
            As a student I've gain experience with various programming languages
            and decided that web design is a thing for me, cause it allows to
            combine my two biggest passions, which are coding and drawing. I
            create websites with the use of frameworks like React.js and Vue.js.
            And for design projects my favourite software solutions are Affinity
            Designer and Affinity Photo.
          </p>
        </div>
      </div>
    );
  }
}

export default Technologies;
