import React, { Component } from "react";
import pic from "../../assets/Muszimuszla/also.png";

class Also extends Component {
  render() {
    return (
      <div className="oneBox">
        <img src={pic} alt="Pic" />
        <div>
          <h1> About me </h1>
          <p>
            {" "}
            I'm a positive, creative person always wanting to broaden my
            knowledge. I'm willing to participate in challenging projects and
            learn new things every day. Always have tons of ideas with hope that
            someday I'll be able to fulfill them all. Above that I also like to
            spend my free time just relaxing and drawing while watching good
            animated movies.{" "}
          </p>
        </div>
      </div>
    );
  }
}

export default Also;
