import React, { Component } from "react";
import pic from "../../assets/Muszimuszla/contact.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faInstagram } from "@fortawesome/free-brands-svg-icons";
import { faArtstation } from "@fortawesome/free-brands-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
class Contact extends Component {
  state = {
    iconSize: "2x"
  };
  render() {
    return (
      <div className="oneBox columnBox">
        <img src={pic} alt="Pic" />
        <div>
          <h1> Contact me </h1>
          <p style={{ textAlign: "center" }}>
            So now you know some things about me. If you'd like to ask me more
            questions or just friendly talk with a cup of coffee don't hasitate
            to contact me.
          </p>
          <div className="socialMediaContact">
            <a href="https://github.com/muszimuszla">
              <FontAwesomeIcon
                icon={faGithub}
                fixedWidth
                color=" rgb(172, 87, 221)"
              />
            </a>
            <a href="https://magdalenasulkowska.artstation.com/">
              <FontAwesomeIcon
                icon={faArtstation}
                fixedWidth
                color=" rgb(172, 87, 221)"
              />
            </a>
            <a href="https://instagram.com/muszimuszla">
              <FontAwesomeIcon
                icon={faInstagram}
                fixedWidth
                color=" rgb(172, 87, 221)"
              />
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;
